import './App.css';
import { Route, Routes } from 'react-router-dom';
import Home from './pages/Home';
import React, { useState } from 'react';
import Who from './components/Who';
import How from './components/How';
import Where from './components/Where.js';
import Fee from './components/Fee';
import About from './components/What';
import Curriculum from './components/Curriculum';

export const Qscontext = React.createContext()

const initialQs = [
  { q: "  ?", a1: "        .    ~", a2: "                   .             ~", a3: "  ,              .     ,     ...     ...      .", path: "/about", id: 0 },
  { q: "  ?", a1: "          .                .", a2: "            ,      .", a3: "              .            .", path: "/who", id: 1 },
  { q: "  ?", a1: "  .       .       .               .    .     .", a2: "    .      50%   .      7~80% .     ,            .          ...        .      . \"   \"", a3: "", path: "/how", id: 2 },
  { q: "   ?", a1: "/  \" \" .", a2: "/  \" 122\" .", a3: "  3.    , ,   .", path: "/where", id: 3 },
  { q: "  ?", a1: "1", a2: "2", a3: "", path: "/curriculum", id: 4 },
  { q: "    ?", a1: "     .               !   ~", a2: "", a3: "", path: "/$$", id: 5 },
]

function App() {
  const [qs, setqs] = useState(initialQs)

  return (

    <Qscontext.Provider value={qs}>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/About" element={<About />} />
        <Route path="/who" element={<Who />} />
        <Route path="/curriculum" element={<Curriculum />} />
        <Route path="/where" element={<Where />} />
        <Route path="/How" element={<How />} />
        <Route path="/$$" element={<Fee />} />
      </Routes>
    </Qscontext.Provider>
  );
}

export default App;
